/**
 * 
 */ 
const App = () => import('@/pages/app/');
const Home = () => import('@/pages/home/');
const NotFound = () => import('@/pages/notFound/');

export default [
  {
    path: '/',
    redirect: '/home',
    component: App,
    children: [
      // home
       {
        path: '',
        redirect: '/home'
      },
      { 
        path: '/home', 
        component: Home
      },
      { // 404 
        path: '/404',
        component: NotFound
      },
      {
        path: '*',
        redirect: '/404'
      },
    ]
  }
  
];
