/**
 * 
 */

const auth = {
  
  USER_KEY: '__REDUXUSER__',

  isLoginIn() {
    const user = JSON.parse(localStorage.getItem(auth.USER_KEY));
    //  cookie 
    if (user) {
      auth.user = user;
      return true;
    } else {
      return false;
    }
  },

  register(user) {
    auth.user = user;
    //  localStorage
    localStorage.setItem(auth.USER_KEY, JSON.stringify(user));
  },

  destroy() {
    localStorage.removeItem(auth.USER_KEY);
  }
};

export default auth;
